import styled from 'styled-components';

import colors from '../utils/colors';

export const Days = styled.ul`
    background: ${colors.primary};
    margin: 0;
    padding: 0;
    padding: 20px;
    border-radius: 0 0 5px 5px;
    display: flex;
    flex-wrap: wrap;
`;



